:root {
  --color-ui-bullet: var(--color-gray-400);
}
html.dark {
  --color-ui-bullet: rgba(255, 255, 255, 0.3);
}
span.success,
b.success {
  color: var(--color-legacy-success-color);
}
span.alert,
b.alert {
  color: var(--color-legacy-alert-color);
}
ul.piped-text {
  list-style: none;
}
ul.piped-text > li {
  float: left;
  border-right: 1px solid var(--color-legacy-lighten);
  margin-right: 10px;
  padding-right: 10px;
  line-height: 18px;
}
ul.piped-text > li:last-of-type {
  margin-right: 0;
  border-right: 0;
}
ul.bulleted-text {
  list-style: none;
  display: inline-block;
}
ul.bulleted-text > li {
  float: left;
  margin-right: 5px;
}
ul.bulleted-text > li .bullet {
  margin-right: 5px;
  font-size: 8px;
  color: var(--color-ui-bullet);
  position: relative;
  top: -1px;
}
ul.bulleted-text > li .bullet:before {
  content: "\2022";
}
ul.bulleted-text > li:first-of-type .bullet {
  display: none;
}
ul.bulleted-text > li.is-active a {
  color: var(--color-legacy-unsat-red);
  cursor: pointer;
}
.ng-cloak {
  display: none;
}
#app,
#shell_content {
  height: 100%;
  min-height: 100%;
}
#shell_content,
#main_footer {
  transition-property: transform;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}
.shift-right #shell_content,
.shift-right #sidenav,
.shift-right #main_footer {
  transform: translate(320px);
}
@media only screen and (min-width: 768px) {
  #shell_content,
  #main_footer {
    transition: none;
  }
  .shift-right #shell_content,
  .shift-right #sidenav,
  .shift-right #main_footer {
    transform: none;
  }
}
#shell {
  padding: 0;
  min-height: calc(100% - 50px);
}
.no-footer #shell {
  min-height: 100%;
}
.full-viewport #shell {
  width: 100%;
}
#shell .sidenav-nubbin {
  position: fixed;
  top: 0;
  width: 50px;
  height: 2em;
  line-height: 2em;
  text-align: center;
  font-size: 2em;
  background: var(--color-ui-bg);
  padding-left: 10px;
  z-index: 100;
}
#shell .sidenav-nubbin i {
  margin-right: 0;
}
#shell_content {
  display: block;
  margin-left: 0;
  padding: 0 15px 15px;
  position: relative;
}
@media only screen and (min-width: 768px) {
  #shell_content {
    margin-left: 55px;
    padding: 0 15px 20px;
  }
}
.no-sidenav #shell_content {
  margin-left: 0;
}
div.flash-msg {
  margin: 0 0 20px;
}
div.flash-msg i {
  position: relative;
  top: 1px;
}
.view {
  height: 100%;
}
