.btn,
a.btn {
  width: auto;
  cursor: pointer;
  display: inline-block;
  font-weight: 400;
  font-size: 12px;
  letter-spacing: 0.2px;
  text-transform: uppercase;
  line-height: 1;
  font-family: var(--font-sans-serif);
  margin: 0;
  padding: 9px 10px 7px;
  position: relative;
  text-align: center;
  text-decoration: none;
  border-radius: 4px;
  border: 1px solid var(--color-ui-button-border);
  transition: background-color 0.15s ease-in-out;
  background-color: var(--color-ui-button-bg);
  color: var(--color-ui-button-text);
}
.btn:not(.is-disabled):hover,
a.btn:not(.is-disabled):hover {
  background-color: var(--color-ui-button-bg-hover);
}
.btn:not(.is-disabled):focus,
.btn:not(.is-disabled):active,
a.btn:not(.is-disabled):focus,
a.btn:not(.is-disabled):active {
  background-color: var(--color-ui-button-bg-focus);
}
.btn.is-scaled,
a.btn.is-scaled {
  width: 100%;
  display: block;
}
.btn.is-link,
a.btn.is-link {
  color: var(--color-ui-link-button-text) !important;
  background-color: transparent;
  border: 0;
}
.btn.is-link:hover,
a.btn.is-link:hover {
  background-color: transparent !important;
